@charset "UTF-8";
@font-face {
  font-weight: 300;
  font-display: swap;
  font-style: normal;
  font-family: "SVN-Gilroy Light";
  src: url("../app/fonts/svngilroy-light.woff2");
}
@font-face {
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  font-family: "SVN-Gilroy Bold";
  src: url("../app/fonts/svngilroy-bold.woff2");
}
:root {
  --font-body: "SVN-Gilroy Light";
  --font-title: "SVN-Gilroy Bold";
  --transition: .2s;
}

:root {
  --card: rgba(112, 112, 112, .1);
  --background: hsl(0, 0%, 13%);
  --footer: hsl(0, 0%, 7%);
  --white: hsl(0, 0%, 100%);
  --blue: hsl(199, 99%, 55%);
  --grey: hsl(0, 0%, 44%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0.875rem;
  color: var(--grey);
  font-family: var(--font-body);
  background-color: var(--background);
}

.info {
  text-align: center;
}

.btn {
  color: var(--white);
  border-radius: 0.25rem;
  padding: 0.625rem 1.125rem;
  text-transform: uppercase;
  background-color: var(--blue);
  border: 0.0625rem solid transparent;
  position: relative;
}
.btn--transparent {
  border-color: var(--white);
  background-color: transparent;
}
.btn--transparent:hover {
  border-color: var(--blue);
  color: var(--blue);
}
.btn + .btn {
  margin-left: 1rem;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-title);
}

p {
  margin: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a,
a:visited,
a:active {
  transition: var(--transition) ease-in-out;
  text-decoration: none;
  color: var(--white);
}

a.link {
  color: var(--blue);
}
a.link::after {
  content: "→";
  min-width: 1.5rem;
  min-height: 1.5rem;
  padding-left: 0.625rem;
}

.title--lg {
  font-size: 3.125rem;
  margin-bottom: 1.75rem;
}
.title--md {
  font-size: 1.75rem;
  color: var(--white);
  margin-bottom: 0.9375rem;
  position: relative;
}
.title--md::before {
  text-transform: uppercase;
  position: absolute;
  top: -3.75rem;
  left: 50%;
  transform: translateX(-50%);
  font-family: var(--font-title);
  font-size: 5rem;
  color: var(--blue);
  opacity: 0.15;
}
.title--sm {
  font-size: 1.5rem;
  color: var(--white);
  margin-bottom: 0.75rem;
}

.text {
  color: var(--white);
  margin-bottom: 1.25rem;
}

section.container {
  padding-top: 12.5rem;
}

.container {
  padding: 0 1.25rem;
  max-width: 73.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  min-height: 6.25rem;
}
.header__nav a {
  position: relative;
}
.header__nav a:not(:last-child) {
  margin-right: 3.75rem;
  padding: 2.5rem 0;
}
.header__nav a:not(:last-child)::after {
  content: "";
  min-width: 100%;
  min-height: 0.25rem;
  transition: var(--transition) ease-in-out;
  background-color: transparent;
  position: absolute;
  left: 0;
  bottom: 0;
}
.header__nav a:hover {
  color: var(--blue);
}
.header__nav a:hover::after {
  background-color: var(--blue);
}
.header__nav a.btn {
  padding-right: 2.125rem;
  padding-left: 2.125rem;
}

.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--white);
}
.hero__info {
  max-width: 31.875rem;
}
.hero__info .btn {
  padding-left: 2.8125rem;
}
.hero__info .btn svg {
  transition: var(--transition) ease-in-out;
  position: absolute;
  fill: var(--white);
  top: 0.75rem;
  left: 1.25rem;
}
.hero__info .btn--transparent:hover svg {
  fill: var(--blue);
}
.hero .title--sm {
  color: var(--blue);
  margin-bottom: 2.25rem;
}
.hero .subtitle {
  font-size: 1.75rem;
  color: var(--grey);
  margin-bottom: 0.9375rem;
}
.hero img {
  padding: 1.375rem 0 3.625rem;
  max-width: 50%;
}

.products {
  text-align: center;
}
.products__items {
  display: flex;
  align-items: center;
  margin-top: 8.125rem;
  justify-content: space-between;
}
.products__item {
  min-width: 22.5rem;
  max-height: 16.25rem;
  background-color: var(--card);
  border-radius: 1.25rem;
}
.products__item img {
  top: -5.625rem;
}
.products__title, .products img {
  position: relative;
}
.products__title {
  font-size: 1.25rem;
  color: var(--white);
  font-family: var(--font-title);
  top: -6.875rem;
}
.products .title--md::before {
  content: "Products";
}

.services__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 5rem;
  gap: 1.875rem;
}
.services__item {
  display: flex;
  align-items: center;
  max-width: 34.6875rem;
  max-height: 11.25rem;
  padding: 1.5rem 1.875rem 1.25rem 1.25rem;
  background-color: var(--card);
  border-radius: 1.25rem;
}
.services__item img {
  margin-right: 1.25rem;
  position: relative;
  top: -1.25rem;
}
.services .title--md::before {
  content: "Services";
}

.members {
  text-align: center;
}
.members__slider {
  position: relative;
}
.members .swiper {
  margin-bottom: 3.75rem;
  position: unset;
}
.members .swiper-slide {
  background-color: var(--card);
  border-radius: 1.25rem;
  min-width: 14.375rem;
  padding-top: 1.25rem;
}
.members .swiper-button-prev, .members .swiper-button-next {
  color: var(--white);
  border-radius: 0.25rem;
  padding: 0.875rem 1.375rem;
  background-color: var(--blue);
}
.members .swiper-button-prev::after, .members .swiper-button-next::after {
  font-size: 1.125rem;
  font-weight: 700;
}
.members .swiper-button-prev {
  left: -1.25rem;
}
.members .swiper-button-next {
  right: -1.25rem;
}
.members .title--md::before {
  content: "Members";
}
.members .text {
  margin-top: 1.25rem;
}

.footer {
  margin-top: 12.5rem;
  color: var(--white);
  background-color: var(--footer);
}
.footer__items {
  display: flex;
  justify-content: space-between;
  padding: 3.4375rem 0 2.375rem;
}
.footer__item h4 {
  margin-bottom: 1.875rem;
  text-transform: uppercase;
}
.footer__item a {
  line-height: 3;
}
.footer__item a:hover {
  text-decoration: underline;
}
.footer .info {
  padding: 1.875rem 0;
  text-transform: uppercase;
  border-top: 0.0625rem solid var(--white);
}